@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background-color: #1a1b21;
    color: #f6fcfd;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    line-height: 1.6;
}

.container {
    background-color: #2a2b30;
    border-radius: 16px;
    max-width: 460px;
    margin: 0 auto;
    margin-top: 10vh;
    padding: 20px 30px;
}

.form-container {
    display: flex;
    width: 100%;
    margin-bottom: 20px;
}

.input-text {
    background-color: transparent;
    border: 1px solid #74b087;
    color: #fff;
    flex: 8 1;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    font-weight: 400;
    padding: 8px;
    max-width: 80%;
    outline: none;
}

    .input-text::-webkit-input-placeholder {
        font-family: 'Open Sans', sans-serif;
        font-weight: 300;
        color: rgb(196, 195, 195);
        opacity: 0.8;
    }

    .input-text:-ms-input-placeholder {
        font-family: 'Open Sans', sans-serif;
        font-weight: 300;
        color: rgb(196, 195, 195);
        opacity: 0.8;
    }

    .input-text::-ms-input-placeholder {
        font-family: 'Open Sans', sans-serif;
        font-weight: 300;
        color: rgb(196, 195, 195);
        opacity: 0.8;
    }

    .input-text::placeholder {
        font-family: 'Open Sans', sans-serif;
        font-weight: 300;
        color: rgb(196, 195, 195);
        opacity: 0.8;
    }

.input-submit {
    background-color: transparent;
    border: 1px solid #74b087;
    color: #74b087;
    cursor: pointer;
    flex: 2 1;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif;
    width: 20%;
    outline: none;
    text-transform: uppercase;
}

    .input-submit:hover {
        background-color: #74b087;
        border: none;
        color: #2a2b30;;
    }

.todo-item {
    list-style-type: none;
    padding: 10px 0px;
    padding-left: 35px;
}

    .todo-item label {
        display: block;
        cursor: pointer;
        font-size: 22px;
        position: relative
    }

        .todo-item label .checkmark {
            background-color: transparent;
            border: 1px solid #fff;
            position: absolute;
            top: 0;
            left: -35px;
            height: 25px;
            width: 25px;
        }

        .todo-item label .checkmark:after {
            content: "";
            position: absolute;
            display: none;
        }

    .todo-item input {
        cursor: pointer;
        height: 0;
        width: 0;
        opacity: 0;
        position: absolute;
    }

        .todo-item input:checked ~ .checkmark:after {
            display: block;
        }

        .todo-item label .checkmark:after {
            left: 8px;
            top: 3px;
            width: 5px;
            height: 10px;
            border: 1px solid #fff;
            border-width: 0 3px 3px 0;
            transform: rotate(45deg);
        }

    .todo-item button {
        background-color: transparent;
        border: 2px solid firebrick;
        border-radius: 50%;
        color: firebrick;
        cursor: pointer;
        float: right;
        font-weight: bold;
        line-height: 16px;
        width: 25px;
        height: 25px;
        outline: none;
    }
